import React from "react";
import { Button, Container, Row, Col, UncontrolledCarousel } from "reactstrap";
import { Link } from 'react-router-dom'

class Carous extends React.Component {
    render() {
        var items = []
        if (this.props.clsname === "carous1") {
            items = [
                {
                    src: require("./Images/BG/18.jpg"),
                },
                {
                    src: require("./Images/BG/19.jpg"),
                }
            ];
        }

        else if (this.props.clsname === "carous3") {
            items = [
                {
                    src: require("./Images/BG/22.jpg"),
                },
                {
                    src: require("./Images/BG/23.jpg"),
                }
            ];
        }

        else if (this.props.clsname === "carous5") {
            items = [
                {
                    src: "https://joeschmoe.io/api/v1/Prabh",
                    width: "300px",
                    height: "400px",
                    header: "Prabhjot Singh",
                },
                {
                    src: "https://joeschmoe.io/api/v1/Kashish Jangid",
                    header: "Kashish Jangid"
                },
                {
                    src: "https://joeschmoe.io/api/v1/boy",
                    header: "Deepak Kapoor"
                },
                {
                    src: "https://joeschmoe.io/api/v1/Rishab Aggarwal",
                    header: "Rishab Aggarwal"
                },
                {
                    src: "https://joeschmoe.io/api/v1/jana",
                    header: "Sumit Anand",
                    width: "700px",
                    height: "400px"
                },
                {
                    src: "https://joeschmoe.io/api/v1/jerry",
                    header: "Samsher Tewatia"
                },
                {
                    src: "https://joeschmoe.io/api/v1/Raj Aryan",
                    header: "Raj Aryan"
                },
                {
                    src: "https://joeschmoe.io/api/v1/jess",
                    header: "Sneha Jha"
                },
                {
                    src: "https://joeschmoe.io/api/v1/jenni",
                    header: "KrishnaPriya"
                }

            ];
        }
        else {
            items = [
                {
                    src: require("./Images/BG/26.jpg"),
                },
                {
                    src: require("./Images/BG/27.jpg"),
                }
            ];

        }

        return (
            <>
                <section className="section section-shaped">
                    <div className={"shape shape-style-1 shape-default " + this.props.clsname}>
                        <span />
                        <span />
                        <span />
                        <span />
                        <span />
                        <span />
                    </div>
                    <Container className="py-md my-80">
                        <Row className="justify-content-between align-items-center">
                            <Col className="mb-5 mb-lg-0" lg="5">
                                <h1 className="text-white font-weight-light">
                                    {this.props.heading}
                                </h1>
                                <p className="lead text-white mt-4">
                                    {this.props.desc}
                                </p>
                                <Link to={this.props.linking} target="_blank">
                                    <Button
                                        className="btn-white mt-4"
                                        color="default"
                                    >
                                        {this.props.heading}
                                    </Button>
                                </Link>
                            </Col>
                            <Col className="mb-lg-auto" lg="6">
                                <div className="rounded shadow-lg overflow-hidden transform-perspective-right">
                                    <UncontrolledCarousel items={items} />
                                </div>
                            </Col>
                        </Row>
                    </Container>
                    {/* SVG separator */}
                    <div className="separator separator-bottom separator-skew">
                        <svg
                            xmlns="http://www.w3.org/2000/svg"
                            preserveAspectRatio="none"
                            version="1.1"
                            viewBox="0 0 2560 100"
                            x="0"
                            y="0"
                        >
                            <polygon className="fill-white" points="2560 0 2560 100 0 100" />
                        </svg>
                    </div>
                </section>
            </>
        );
    }
}

export default Carous;
